#ui-id-1{
    z-index: 500 !important;
    background: white;
    height: 25vh;
    overflow-x: hidden;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    padding: 1rem !important;
    border: solid 1px #e8e8e8;

}
/* Ponemos un color de fondo y redondeamos las esquinas del thumb */
#ui-id-1::-webkit-scrollbar-thumb {
    background: #e8e8e8 !important;
    border-radius: 5px !important;
}
/* Cambiamos el fondo y agregamos una sombra cuando est en hover */
#ui-id-1::-webkit-scrollbar-thumb:hover {
    box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2) !important;
}
/* Cambiamos el fondo cuando est en active */
.ui-menu {
   z-index: 500 !important;
   background: white;
   height: 28vh;
   overflow-x: hidden;
   border-radius: 3px;
   padding: 0 1%;
   position: fixed !important;
}
.ui-menu-item {
    list-style: none;
    padding-top: 3%;
    height: 3rem;
}
.ui-menu-item > .ui-menu-item-wrapper{
    display: flex;
    color: black !important;
    font-size: 1rem !important;
    height: 50px;
    width: 100%;
    padding: .5rem 0;
    margin-left: 5px;
    border-radius: 5px;
    align-items: center;
}
.ui-menu-item > .ui-menu-item-wrapper > i{
    margin-left: 10px;
}
.ui-menu-item > .ui-menu-item-wrapper:hover,
.ui-menu-item > .ui-menu-item-wrapper:hover > i,
.ui-menu-item > .ui-state-active,
.ui-menu-item > .ui-state-active > i {
    background: #e8e8e8 !important;
}
.ui-menu-item > .ui-menu-item-wrapper > i{
    border: solid 1px black;
    border-radius: 50px;
    padding: 5px;
    margin-right: 10px;
}
.ui-menu-item > .ui-menu-item-wrapper > i:hover{
    border: solid 1px white;
}
.ui-menu-item > .ui-menu-item-wrapper > span > small{
    margin-top: -9px;
    display: inherit;
}
.ui-menu li::marker {
    color: #0083D6 !important;
}
.ui-helper-hidden-accessible{
    display: none !important;
}
