// Seccin tarjetas profesionales de al salud
 .container_entidades {
    margin: auto;
    padding: 0;
    justify-content: flex-start;
    margin-left: 16%;
}
.content_img_entidades {
    display: flex;
    justify-content: center;
    align-self: center;
}
// Seccin boton ver especialidades
.section_btn_entidades { 
    display: flex;
    justify-content: center;
}
.logosUniversidades {
    width: 50%; 
}
.contains_slider-logos-galeriaProf {
    max-width: 100%;
    display: flex;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
}
